@import url('https://fonts.googleapis.com/css2?family=Arvo:wght@400;700&family=Inter:wght@100&display=swap');
/* Variables */
$pink: #E11299;
$wedding:#F5C6EC;
$white:#fff;
$base-color:$pink;

/* Mixin's */
@mixin transition {
    transition: all 0.5s ease-in-out;
}
@mixin corners ($radius) {
    border-radius: $radius;
}

body {
    background: $base-color;
    font-family: 'Arvo', serif;
    height: 100%;
}
.wrapper {
   width: 800px;
   margin: 30px auto;
   color: $white;
   text-align: center;
}

h1, h2, h3 {
    font-family: 'Arvo', serif;
    font-weight: 400;  
    font-size: 2.6em;
    text-transform: uppercase;
}
#seconds, #tens{
    font-size: 2em;
}
button{
    @include corners(5px);
    background: $base-color;
    color: $white;
    border: solid 1px $white;
    text-decoration: none;
    cursor: pointer;
    font-size: 1.2em;
    padding: 18px 10px;
    width: 180px;
    margin: 10px;
    outline: none;
     &:hover{
        @include transition;
        background: $white;
        border: solid 1px $white;
        color: $base-color;
     }
}